.makeYellow{
    color: rgb(254, 194, 0);
    cursor: pointer;
}

.headerText h1{
    color: rgb(24, 24, 24);
    font-family: PP Neue Montreal;
    font-size: 33px;
    font-weight: 400;
    line-height: 110%;
    letter-spacing: 0.6px;
    text-align: center;
}

.midText{
margin-bottom: 20px;
}

.midText p{
    margin: 5px 0;
    color: rgb(24, 24, 24);
    font-family: PP Neue Montreal;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.6px;
    text-align: center;
}

.midText p br{
    display: inline;
}

.btnCont{
    display: inline-flex;
    flex-direction: column;
    gap: 20px;
    margin-bottom: 20px;
}

.lowerText{
    font-family:PP Neue Montreal;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.6px;
    text-align: center;
}

.container{
    max-width: 300px;
    text-align: center;
    margin: 0 auto;
    width: 100%;
}

@media (min-width: 765px) {

    .midText p br{
        display: none;
    }

    .btnCont{
        display: inline-flex;
        flex-direction: row;
        gap: 20px;
        margin-bottom: 30px;
    }

    .midText{
        margin-bottom: 50px;
    }

    .headerText h1{
        font-size: 36px;
        font-weight: 400;
        line-height: 40px;
        letter-spacing: -1px;
    }

    .container{
        max-width: 800px;
    }

    .midText p{
        font-size: 16px;
        font-weight: 400;
        line-height: 20px;
        letter-spacing: 0.6px;
    }

    .lowerText{
        font-size: 16px;
        font-weight: 400;
        line-height: 20px;
    }
}