@import url(https://fonts.cdnfonts.com/css/pp-neue-montreal);

.DropdownLabel {
    font-family: 'PP Neue Montreal', 'sans-serif';
    font-size: 16px;
    font-weight: 400;
    line-height: 30px;
    letter-spacing: 0.6px;
    text-align: left;
}

.DropdownError {
    font-family: 'PP Neue Montreal', 'sans-serif';
    font-size: 14px;
    font-weight: 400;
    line-height: 30px;
    letter-spacing: 0.6px;
    text-align: left;
    color: red;
}

.storybook-dropdown {
    border: 1px solid #D9D9D9;
    border-radius: 8px;
    padding: 0 20px;
    font-size: 16px;
    font-weight: 400;
    color: black;
    outline: none;
    font-family: Montserrat,PP Neue Montreal;
    line-height: 140%;
    letter-spacing: 0.6px;
    text-align: left;
    width: 530px;
    height: 44px;
    appearance: none;
    background-size: 25px;
    background: url('data:image/png;base64,') no-repeat right 10px center;
    background-color: white;
}

.storybook-dropdown:focus {
    border: 1px solid rgb(39, 39, 39);
    border-radius: 8px;
    background-size: 25px;
    background: rgb(254, 194, 0)  url('data:image/png;base64,') no-repeat right 10px center;
    color: black;
}

.storybook-dropdown--small {
    width: 260px;
    height: 44px;
    padding: 0 10px;
}

.storybook-dropdown--medium {
    width: 504px;
    height: 44px;
    padding: 0 15px;
}

.storybook-dropdown--large {
    width: 530px;
    height: 44px;
    padding: 0 20px;
}

.storybook-dropdown--error {
    box-sizing: border-box;
    border: 1px solid rgb(216, 73, 16);
    border-radius: 8px;
    background-size: 25px;
    background: rgb(255, 235, 170) url('data:image/png;base64,') no-repeat right 10px center;
}

.storybook-dropdown--disabled {
    cursor: not-allowed;
    color: rgb(217, 217, 217);
    box-sizing: border-box;
    border: 1px solid rgb(217, 217, 217);
    border-radius: 8px;
    background-size: 25px;
    background: white   url('data:image/png;base64,') no-repeat right 10px center ;
}

.storybook-dropdown--disabled::placeholder {
    color: rgb(217, 217, 217);
}

.storybook-dropdown option:hover {
    background-color: rgba(254, 194, 0, 0.2);
}

.DropdownLabel--small {
    font-size: 16px;
    line-height: 30px;
}

.DropdownLabel--medium {
    font-size: 16px;
    line-height: 35px;
}

.DropdownLabel--large {
    font-size: 16px;
}

.error {
    color: red;
}

.disabled {
    color: rgb(217, 217, 217);
    cursor: not-allowed;
}

.default {
    color: rgb(24, 24, 24);
}

/*.focused {*/
/*    color: rgb(254, 194, 0);*/
/*}*/

.storybook-dropdown option:hover{
    background: black;
    color: white;
}
.customDropdown {
    color: black;
    background-color: white;
    font-size: 16px;
    /* Add more custom styles as needed */
}


.dropdownHeader{
    font-family: 'PP Neue Montreal', 'sans-serif';
    font-size: 16px;
    font-weight: 400;
    line-height: 30px;
    letter-spacing: 0.6px;
    border: #d7d7d7 1px solid;
    border-radius: 8px;
    height: 44px;
    display: inline-flex;
    padding: 0 10px;
    align-items: center;
    cursor: pointer;
    justify-content: space-between;
    box-sizing: border-box;
}

.dropdownList{
    font-family: 'PP Neue Montreal', 'sans-serif';
    font-size: 16px;
    font-weight: 400;
    line-height: 30px;
    letter-spacing: 0.6px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    /*padding: 10px;*/
    position: absolute;
    top: 50px;
    width: 502px;
    border-radius: 8px;
    background: white;
    overflow-y: scroll;
    height: 200px;
}

.container{
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
}

.dropdown{
    position: relative;
}

.dropdownHeader.error {
    border-color: red;
}

.dropdownHeader.focused {
   background: rgb(254, 194, 0);
    border: black 1px solid;
}

.dropdownItem{
    padding: 10px;
    border-radius: 8px;
}

.dropdownItem:hover{
    background: black;
    color: white;
    cursor: pointer;
}

.Dropdown--small{
    width: 262px;
}

.Dropdown--medium{
    width: 397px;

}

.Dropdown--large{
    width: 480px;
}

@media (min-width: 768px){
    .Dropdown--small{
        width: 508px;
    }
}

@media (min-width: 1024px){
    .Dropdown--small{
        width: 530px;
    }
}